<template>
  <div class="row max-height-vertical box-overflow overflow-hidden">
    <div class="row scroll-outer">

      <div class="span-14-lg offset-1-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs">
        <p class="large">{{content.title}}</p>
      </div>

      <!-- todo: format to handle multiple bold lines -->
      <div class="relative span-6-lg offset-1-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs bg-white share-flip-outer no-ratio-md transform-up" :style="ratio_styles">

        <div class="scroll-outer">
          <div class="share-flip-inner">
            <p><strong>{{content.left_card.bold_text}}</strong> {{content.left_card.paragraph}}</p>
          </div>
        </div>

      </div>

      <div class="relative span-6-lg offset-2-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs bg-white share-flip-outer no-ratio-md" container-card="share-card" :style="ratio_styles">
        
        <div class="scroll-outer">
          <div class="share-flip-inner">
            <p><strong>{{content.right_card.bold_text}}</strong> {{content.right_card.paragraph}}</p>
          </div>
        </div>

      </div>

    </div>
  </div>
</template>

<style scoped lang="scss">
  @import '../../styles/variables';
  
</style>

<script>
  import header_block from '../../components/organisms/header_block'
  import share_card from '../../components/organisms/share_card'

  export default {
    props: ['content'],
    data () {
      return {
        ratio_styles: {}
      }
    },
    methods: {
      // todo: make reusable
      ratio_height () {
        let el = document.querySelector('[container-card="share-card"]')
        let styles = {
          'height': el.offsetWidth * 1/1.25 + 'px',
        }
        this.ratio_styles = styles
      },
    },
    components: {
      'header-block': header_block,
      'share-card': share_card
    },
    mounted () {
      this.ratio_height()
      window.addEventListener('resize', this.ratio_height)
    }
    // todo: add is active method
  }
</script>


