<template>
  <full-span-slide>
    
    <p class="large">{{content.title}}</p>

    <div class="row">
      <radio-span v-for="option in content.options" v-bind:key="option.index" v-bind:strong="option.strong" v-bind:correct="option.correct">{{option.title}}</radio-span>
    </div>

  </full-span-slide>
</template>

<script>
  import full_span_slide from '../../components/organisms/full_span_slide'
  import radio_span from '../../components/molecules/radio_span'

  export default {
    props: ['content', 'active'],
    components: {
      'full-span-slide': full_span_slide,
      'radio-span': radio_span
    },
    methods: {
      // make reusable
      is_active () {
        if (this.active && !this.$store.state.checkbox.el) {
          this.$store.commit('set_navigation_next', {
            custom: 'button-disabled'
          })
        } else {
          this.$store.commit('set_navigation_next', {
            custom: ''
          })
        }
      }
    },
    watch: {
      active () {
        this.is_active()
      }
    }
  }
</script>


