<template>
        <div class="row bg-white max-height-vertical box-overflow overflow-hidden">
          <div class="row scroll-outer">
          <div class="span-16-lg relative">
            
            <div class="relative ind-1 full-span-outer">
              <div class="full-span-inner">
                <slot></slot>
              </div>
            </div>
            
            <!-- <crosshair-background></crosshair-background>
            <span-background></span-background> -->

          </div>
          </div>
        </div>
</template>

<script>
  import crosshair_background from '../atoms/crosshair_background'
  import span_background from '../atoms/span_background'

  export default {
    components: {
      'span-background': span_background,
      'crosshair-background': crosshair_background
    }
  }
</script>


<style scoped lang="scss">

  @import '../../styles/variables';

  .full-span-outer {
    position: relative;
    box-sizing: border-box;
    display: flex;
    height: 100vh;
    max-height: 100%;
    @media screen and (max-width: $md-max){
      height: auto;
      padding-top: 48px;
      padding-bottom: 48px;
    }
  }

  .full-span-inner {
    position: relative;
    margin: auto;
    max-width: 100% * 5/8;
    @media screen and (max-width: $sm-max) {
      max-width: calc(100% - 24px - 24px - 12px); // additional 12 overall added to make less cramped
    }
  }

</style>



