export default new Vuex.Store({
  state: {
    example_pages,
    page: 0,
    notification_state: {
      title: 'title here',
      text: 'text here',
      active: false
    },
    checkbox: {
      el: null,
      correct: 'incorrect'
    },
    navigation: {
      back: {
        text: 'back',
        custom: ''
      },
      next: {
        text: 'next',
        custom: ''
      }
    }
  },
  mutations: {
    change_page (state, inc) {
      let new_page = state.page + inc
      if (-1 < new_page && new_page < state.example_pages.length) {
        state.page = new_page
      }
    },
    set_navigation_next (state, payload) {
      Object.assign(state.navigation.next, payload)
    },
    set_navigation_back (state, payload) {
      Object.assign(state.navigation.back, payload)
    },
    set_notification (state, payload) {
      Object.assign(state.notification_state, payload)
    },
    set_checkbox (state, payload) {
      // state.checkbox = payload
      Object.assign(state.checkbox, payload)
    }
  }
})




