<template>
  <div class="row max-height-vertical box-overflow overflow-hidden">
    <div class="row scroll-outer">
    
      <div class="span-14-lg offset-1-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs">
        <p class="large">{{content.title}}</p>
      </div>
        
      <flip-card v-for="card in content.cards" v-bind:key="card.index" v-bind:icon="card.icon" v-bind:title="card.title" v-on:flip="change_flip_count">
        {{card.paragraph}}
      </flip-card>

    </div>
  </div>
</template>

<script>
  import flip_card from '../../components/molecules/flip_card'
  import header_block from '../../components/organisms/header_block'

  export default {
    props: ['content', 'active'],
    components: {
      'header-block': header_block,
      'flip-card': flip_card
    },
    data () {
      return {
        flipped: 0
      }
    },
    methods: {
      change_flip_count (inc) {
        this.flipped += inc
        this.is_active()
      },
      // make reusable (here and progress page)
      is_active () {
        if (this.active && this.flipped < this.content.cards.length) {
          this.$store.commit('set_navigation_next', {
            custom: 'button-disabled'
          })
        } else {
          this.$store.commit('set_navigation_next', {
            custom: ''
          })
        }
      }
    },
    watch: {
      active () {
        this.is_active()
      }
    }
  }
</script>



