<template>
  <div class="row max-height-vertical box-overflow overflow-hidden">
    <div class="row scroll-outer">


    <div class="span-14-lg offset-1-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs">
      <p class="large">{{content.title}}</p>
    </div>

    <div class="span-9-lg offset-1-lg span-8-md overflow-hidden max-height-selector box-overflow overflow-hidden no-border">
      <progress-selector v-for="(option, index) in content.options" v-bind:key="index" v-bind:index="index" v-bind:activeindex="offset_index" v-on:clicked="set_card(option.content, index)" v-bind:transform="transform_style">{{option.title}}</progress-selector>
    </div>
  
    <div class="span-4-lg offset-1-lg span-7-md offset-1-md span-16-sm span-16-xs bg-white center-flex" container-card="selector-card" :style="ratio_styles">
      <div v-if="card" class="card-inner">
        
        <component v-bind:is="card.icon"></component>
        <p class="subheader upcase">{{card.title}}</p>
        <p>{{card.paragraph}}</p>

      </div>
    </div>
    

    </div>
  </div>
</template>

<style scoped lang="scss">
  @import '../../styles/variables';

  .max-height-selector {
    position: relative;
    min-height: calc(72px * 3);
    max-height: calc(100vh - 96px - 98px - 48px); // todo: set this with js

    @media screen and (max-width: $md-max) {
      height: auto;
      max-height: calc(72px * 7);
    }
  }

  .card-inner {
    position: relative;
    text-align: center;
  }
</style>

<script>

  import progress_selector from '../../components/molecules/progress_selector'
  import rollover_card from '../../components/molecules/rollover_card'
  import header_block from '../../components/organisms/header_block'
  import svg from '../../components/svg'

  export default {
    props: ['content', 'active'],
    data () {
      return {
        card: null,
        offset_index: 0,
        multiplier: 72,
        ratio_styles: {}
      }
    },
    methods: {
      set_card (content, index) {
        if(index === this.offset_index) {
          this.card = content
          this.offset_index += 1
          this.is_active()
        }
      },
      // make reusable
      ratio_height () {
        let el = document.querySelector('[container-card="selector-card"]')
        let styles = {
          'height': el.offsetWidth * 4/3 + 'px',
        }
        this.ratio_styles = styles
      },
      // make reusable
      is_active () {
        // todo: keep some internal logic, but take base template (back active next disabled) and render on global scope.
        if (this.active && this.offset_index < this.content.options.length) {
          this.$store.commit('set_navigation_next', {
            custom: 'button-disabled'
          })
        } else {
          this.$store.commit('set_navigation_next', {
            custom: ''
          })
        }
      }
    },
    computed: {
      transform_style () {
        let offset =  (this.offset_index * this.multiplier) - this.multiplier
        return {
          'transform': 'translateY(-' + offset + 'px)'
        }
      }
    },
    components: {
      'progress-selector': progress_selector,
      'rollover-card': rollover_card,
      'header-block': header_block,
      ...svg
    },
    mounted () {
      this.ratio_height()
      window.addEventListener('resize', this.ratio_height)
    },
    watch: {
      active () {
        this.is_active()
      }
    }
  }
</script>



